body {
  background-color: #0093e9;
  background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
  background-size: cover;
  min-height: 100vh;
}

h1 {
  color: #fff;
}

.container {
  text-align: center;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  padding: 50px;
  display: flex;
}

.btn-list {
  margin-top: 50px;
}

textarea {
  color: #000;
  border: 1px solid #000;
  width: 500px;
  height: 100px;
  margin: 50px 0;
  padding: 10px;
}

.drag_drop {
  border: 2px solid #000;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 400px;
  margin-top: 50px;
  transition: all .3s linear;
  display: flex;
}

.drag_drop:hover {
  color: #fff;
  border: 3px dashed #fff;
  transition: all .25s linear;
}

label {
  cursor: pointer;
}

input[type="file"] {
  display: none;
}

